@use 'vars.scss';


.product-item {
    width: 240px;
  }
  .product-item-image{
    width: 240px;
    height: 240px;
    border-radius: 20px;
    object-fit: cover;
    cursor: pointer;
  }
  .product-card-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 12px;
  }
  .product-card-info figure {
    margin: 0;
  }
  .add-to-cart-icon {
    height: 40px;
    cursor: pointer;
  }
  .product-card-info div p:nth-child(1) {
    font-weight: bold;
    font-size: var(--md);
    margin-top: 0;
    margin-bottom: 4px;
  }
  .product-card-info div p:nth-child(2) {
    font-size: var(--sm);
    margin-top: 0;
    margin-bottom: 0;
    color: var(--very-light-pink);
  
  }